
import { axiosUtils } from '../_helpers';
import urlConfig from 'url.config'

export const invoiceService = {
    getAllInvoices,
    getInvoiceInfo,
    createInvoice,
    updateInvoice,
    updateInvoiceStatus,
    removeInvoice
};

async function getAllInvoices(request) {
    return await axiosUtils.get(urlConfig.INVOICE_GET_ALL + `/${request.projectId}`);
}

async function getInvoiceInfo(invoiceId) {
    return await axiosUtils.get(urlConfig.INVOICE_GET_INFO + `/${invoiceId}`);
}

async function createInvoice(invoiceRequest) {
    return await axiosUtils.post(urlConfig.INVOICE_CREATE, invoiceRequest);
}

async function updateInvoice(invoiceRequest) {
    return await axiosUtils.put(urlConfig.INVOICE_UPDATE, invoiceRequest);
}
async function updateInvoiceStatus(invoiceId, status) {
    return await axiosUtils.put(urlConfig.INVOICE_UPDATE + `/status/${invoiceId}/${status}`);
}

async function removeInvoice(invoiceId) {
    return await axiosUtils.remove(urlConfig.INVOICE_REMOVE + `/${invoiceId}`);
}
